
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../services';

export default {
directives: {
TransferDom
},
components: {
Actionsheet,
XHeader,
Group,
Cell,
XTextarea,
Toast,
CellBox,
Popup
},
data() {
return {
payShow:false,
menus:{
menu1: '',
menu2: ''
},
cardstate: {
id: "",
storeId: "",
},
priceShow: false,
currentPrice: 0,
currentCard: 0,
cardShow: false,
confrim: "",
toast: false,
toasts: false,
/*  */
tpl: {},
orderLogistics: '/orderLogistics/',
/*  */
haveDefault: false,
/* id */
cartId: 0,
/*  */
infoData: {},
/*  */
timeMap: { 1: "", 2: "", 3: "", 4: "", 5: "" },
/*  */
tplRules: {},
/*  */
option: "",
paydata: {

},
cardList: [],
/*  */
payMethod: 4,
payTypeData:null,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
/*  */
returnTpl() {
this.getTplPrice();
if (this.$store.state.tplId == 1) {
return "";
} else if (this.$store.state.tplId == 2) {
return "";
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return "";
} else {
return "";
}
},
/*  */
rentRange() {
let self = this;
let start = dateFormat(new Date(self.infoData.cart_start_time * 1000), 'YYYY-MM-DD');
let end = dateFormat(new Date(self.infoData.cart_end_time * 1000), 'YYYY-MM-DD');
return { start, end };
},
/*  */
/*  */
timeText() {
return this.timeMap[this.infoData.rent_period_type];
},
/*  */
goodsDespoit() {
if (this.infoData.goods_deposit == 0) {
return 0;
}
let despoite = this.infoData.goods_deposit * this.infoData.cart_content_good_amount - this.goodsAllPrice;
if (despoite <= 0) {
return 0;
} else {
return despoite;
}
},
/*  */
/* **++ */
goodsAllPrice() {
let Price = this.infoData.rent_period_now_rent_price * this.infoData.cart_time_number * this.infoData.cart_content_good_amount;
let allPrice = Price + this.currentPrice;
return allPrice <= 0 ? 0 : allPrice;
},
/*  */
returnTplPrice() {
let num = this.infoData.cart_content_good_amount;
let second = num - 1;
if (this.$store.state.tplId == 1) {
return this.tplRules.freight_first_price + this.tplRules.freight_continued_price * second - 0;
} else if (this.$store.state.tplId == 2) {
return this.tplRules.freight_door_cost * num - 0;
} else if (this.$store.state.tplId == 3) {
this.tpl = this.$store.state.sinceData;
return 0;
} else {
return 0;
}
}
},
methods: {
browserPay(key){
if(key=='menu1'){
/*  */
this.payMethod=5;
}else if(key=='menu2'){
this.payMethod=4;
}else{
return false;
}
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,//
rent_period_id: this.infoData.cart_content_rent_prieod_id,//
returnTime: this.infoData.cart_end_time,//
sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
self.payTypeData=resopndy.body;
if(key=='menu1'){
/*  */
window.location.href=this.payTypeData;
}else if(key=='menu2'){
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});

},
/*   0  1  2 */
isAlipay() {
var userAgent = navigator.userAgent.toLowerCase();
if (userAgent.match(/Alipay/i) == "alipay") {
this.payMethod = 4;
return 1;
} else if (userAgent.match(/MicroMessenger/i) == "micromessenger") {
this.payMethod = 3;
return 0;
} else {
return 2;

}
},
goShop(item) {
this.cardstate.id = item.coupon_no;
this.cardstate.storeId = item.store_id;
this.currentPrice = 0 - item.amount;
this.priceShow = true;
this.cardShow = false;
},
closeCard() {
this.cardShow = false;
},
/*  */
cardshow() {
if (this.cardList.length == 0) {
this.confrim = "";
this.toast = true;
return false;
}
this.cardShow = true;
},
onBridgeReady() {
let self = this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest', self.paydata,
function(res) {
localStorage.setItem("reload", "1");
if (res.err_msg == "get_brand_wcpay_request:fail") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
})

}, 500);
}
if (res.err_msg == "get_brand_wcpay_request:cancel") {
self.$router.push({
path: '/index/main/order'
})

}
if (res.err_msg == "get_brand_wcpay_request:ok") {
self.confrim = "";
self.toast = true;
setTimeout(() => {
self.$router.push({
path: '/index/main/order'
})

}, 500);
}     // ,res.err_msg    ok
}
);
},
routerBack() {
this.$router.goBack();
},
/*  */
buygoods() {

/*  */
if (this.getAddress == "") {
this.confrim = "";
this.toasts = true;
return false;
}
if (this.returnTpl == "") {
this.confrim = "";
this.toasts = true;
return false;
}
let self=this;
if(this.isAlipay()==0){
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,//
rent_period_id: this.infoData.cart_content_rent_prieod_id,//
returnTime: this.infoData.cart_end_time,//
sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
/*  */
this.paydata = resopndy.body;
if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
} else if (document.attachEvent) {
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
} else {
self.onBridgeReady();
}
}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});
}else if(this.isAlipay()==1){
API.order.orderPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
info: [{
addressId: this.$store.state.tplId != 3 ? this.$store.state.addressData.id : "",//id 
cartId: this.cartId, //id
cart_tpl: this.$store.state.tplId,//
freight_continued_price: this.tplRules.freight_continued_price,//
freight_door_cost: this.tplRules.freight_door_cost,//
freight_first_price: this.tplRules.freight_first_price,//
goodsAmount: this.infoData.cart_content_good_amount,//
order_time_number: this.infoData.cart_time_number,//
rentTime: this.infoData.cart_start_time,//
rent_period_id: this.infoData.cart_content_rent_prieod_id,//
returnTime: this.infoData.cart_end_time,//
sinceId: this.$store.state.tplId == 3 ? this.$store.state.sinceData.since_id : "",//id
coupon_no: this.cardstate.id,
message: this.option,
}]
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}, (err) => {
self.confrim = "";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});
}else{
this.payShow=true;
}

},
/*  */
Initialization() {
this.cartId = this.$route.params.id;
API.order.orderConfirm({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
cartId: [this.cartId],
}).then((res) => {
this.infoData = res.body.data.cart_list[0];
this.orderLogistics = "/orderLogistics/" + this.infoData.cart_content_goods_id;
API.card.storeCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
storeId: this.infoData.store_id,
}).then((res) => {
if (res.body.code == 200) {
this.cardList = res.body.data;
}
});
});
this.getDefaultAddress();


/*  */
this.$store.dispatch('ClearTpl');
},
/* vuex */
getDefaultAddress() {
API.person.addressInfo({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
is_default: 1
}).then((res) => {
if (res.body.code == 200) {
this.haveDefault = true;
res = res.body.data.addressList;
this.$store.dispatch('SetAddress', res);
} else {
this.haveDefault = false;
}
});
},
/*  */
getTplPrice() {
if (this.$store.state.addressData.detail != "") {
API.order.tplPrice({
tpl: this.$store.state.tplId,
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.infoData.cart_content_goods_id
}).then((res) => {
if (res.body.code == 200) {
this.tplRules = res.body.data;

}
});
}
}
}, mounted() {
overscroll(document.querySelector('.orderInfo_main_content'));
/*  */
this.Initialization();
},
activated() {
overscroll(document.querySelector('.orderInfo_main_content'));
/*  */
if (localStorage.getItem('orderClick')) {
localStorage.setItem('orderClick', '');
this.Initialization();
this.cardstate = {
id: "",
storeId: "",
};
this.priceShow = false;
this.currentPrice = 0;
}
/*  */
if (localStorage.getItem('addressClick')) {
localStorage.setItem('addressClick', '');
this.haveDefault = true;
}
}
}



